*
{
    font-family: Tahoma;
}

.imgContainer-circle
{
    border-radius: 50%;
    overflow: hidden;
    display: flex;
    justify-content: center;
}
    .imgContainer-circle > img
    {
        max-width: 100%;
        max-height: 100%;
    }

.clickable
{
    cursor: pointer;
}

/* width */
::-webkit-scrollbar {
    width: 20px;
  }
  
  /* Track */
  ::-webkit-scrollbar-track {
    box-shadow: inset 0 0 5px grey; 
    border-radius: 10px;
  }
   
  /* Handle */
  ::-webkit-scrollbar-thumb {
    background: #0fbaf2; 
    border-radius: 10px;
  }
  
  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
    background: #0f99c7; 
  }


.entry-display
{
    overflow: hidden;
    display: grid;
    grid-template-rows: auto 1fr auto;
}
.footer
{
    background-color: #33383e;
    /* background-color: #505861 */
    color: white;
    text-align: center;
    border: 2px solid black;
    margin-top: 8px;
}
.header
{
    background-color: #33383e;
    width: 100%;

    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.social-btn-container
{
    width: 4em;
    height: 4em;
    padding: 0;
    border-radius: 8%;
    border: 2px solid black;
}
.social-btn-container > *
{
    font-size: 2.2em;
}
.header-body-container
{
    color: black;
    width: 100%;

    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;

    margin-top: 20px; 
}
    .profile-img
    {
        
        border: 2px solid white;
        width: 13em;
        height: 13em;
    }
        .profile-img > img
        {
            width: 100%;
            height: 100%;
        }
    .header-body-summary
    {
        margin-left: 0px;
        overflow: hidden;
    }
        .header-descr
        {
            margin-top: 1em;
            text-align: center;
            color: white; 
            /* border: 2px solid green; */
            /* width: 45em; */
        }
        .pronunciation
        {
            color: white;
        }

@media only screen and (max-width: 2560px) 
{
    .header-body-summary
    {
        width: 30%;
    }
}
@media only screen and (max-width: 1440px) 
{
    .header-body-summary
    {
        width: 50%;
    }
}
@media only screen and (max-width: 768px) 
{
    .header-body-summary
    {
        width: 90%;
    }
}

/* Contact Links */

.contact-container
{
    display: flex;
    margin-top: 1em;
    width: 100%;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
}

    #resume-btn
    {
        border: 2px solid black;
        background-color: whitesmoke;
        border-radius: 6%;
        width: 8em;
        height: 4em;
        margin-bottom: 1em;
        font-weight: bold;
    }
        #resume-btn:hover
        {
            background-color: #ffc125;
            cursor: pointer;
        }

.social-media-container
{
    margin-left: 2em;
    
    width: 20em;

    display: flex;
    justify-content: space-evenly;
}

.nav
{
    display: flex;
    flex-direction: row;
    grid-column-gap: 14rem;
    -webkit-column-gap: 14rem;
            column-gap: 14rem;
    margin: auto;
    overflow: hidden;
}

    .entry-toggle-container
    {
        display: flex;
        flex-direction: row;
        grid-column-gap: 0.4rem;
        -webkit-column-gap: 0.4rem;
                column-gap: 0.4rem; /* Media */
        height: 5rem;
    }
        .switch-label
        {
            margin: auto;
        }
        .entry-toggle-container > span
        {
            font-size: 2.2em;
            transition: color ease-in 0.2s;
            margin-top: 10px;
            font-family: "Staatliches";
            text-align: center;
            padding-top: 6px;
        }
    
    .active
    {
        color: white;
        text-shadow: 0 0 5px white;
        -webkit-animation: glow 3s ease-in;
                animation: glow 3s ease-in;

    }

    .inactive
    {
        color: black;
        text-shadow: 0 0 2px white;

    }

    @-webkit-keyframes glow {
        from {
            text-shadow: 0 0 10px black;
        }
        to {
            text-shadow: 0 0 10px white; 
        }
    }


    /* mobile small */

    @media only screen and (max-width: 320px) {
        /* For desktop: */
        .entry-toggle-container
        {
            display: flex;
            flex-direction: row;
            grid-column-gap: 0.4rem;
            -webkit-column-gap: 0.4rem;
                    column-gap: 0.4rem; /* Media */
            height: 5rem;
        }

        .entry-toggle-container > span
        {
            font-size: 1.8em;
            padding-top: 10px;
        }
    }
.entry-container
{
    background-image: url(/static/media/cardboard.a61f8066.png);
    overflow: hidden;
    border: 2px solid black;
    margin-top: 8px;

    /* display: grid;
    grid-template-rows: auto 1fr auto; */

    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.entry-container > * 
{
    /* border: 20px solid pink; */
}
    .entry-title
    {
        text-align: center;
    }
    
        
.entry-body
{
    border-top: 2px solid black;
    width: 100%;

    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;

    margin-bottom: 12px; 
}
    .entry-body > *
    {
        margin: auto;
    }
    .entry-media
    {
        display: inline-block;
        width: 20em;
        height: auto;
        vertical-align: middle;
        margin-top: 16px;
        margin-bottom: 25px;
    }
        .entry-media > *
        {
            max-width: 100%;
            max-height: 100%;
            vertical-align: middle;
            object-fit: cover;
            box-shadow: 5px 10px 8px #888888;
        }
        .entry-video
        {
            width: 100%;
            height: 19.4em;
        }

    .entry-description
    {
        display: inline-block;
        width: 90%;
    }
        .entry-summary
        {
            height: 8em;
            overflow-y: scroll;
            text-align: left;
            margin-bottom: 12px;
            background-color: whitesmoke;
            padding: 10px;
            box-shadow: 5px 10px 8px #888888;
        }
        .technologies-container
        {
            display: flex;
            flex-wrap: wrap;
            flex-direction: row;
            justify-content: space-around;
            grid-column-gap: 8px;
            -webkit-column-gap: 8px;
                    column-gap: 8px;
            grid-row-gap: 8px;
            row-gap: 8px;
            overflow-y: scroll;

        }

        .entry-summary::-webkit-scrollbar, .technologies-container::-webkit-scrollbar {
            display: none;
        }
        

@media only screen and (min-width: 1024px) and (max-width: 2560px) {
    .entry-description
    {
        width: 40%;
    }
} 

@media only screen and (min-width: 1024px) and (max-width: 1440px) {
    .entry-description
    {
        width: 60%;
    }
} 

@media only screen and (max-width: 768px) {
    
    .entry-media
    {
        width: 20.5em;
    }
} 

@media only screen and (max-width: 375px) {
    
    .entry-media
    {
        width: 16.5em;
    }
} 

