.tech-container
{
    background-color: whitesmoke;
    overflow: hidden;
    
    border: 2px solid black;
    display: flex;
    border-radius: 8%;
    flex-direction: row;
    justify-content: center;
    padding: 0px;
    width: 10em;
    box-shadow: 2.5px 5px 4px #888888;
}

    .tech-icon
    {
        color: black;
        font-size: 1.3em;
        margin-right: 8px;
    }
    .tech-name
    {
        font-weight: bold;
        text-transform: capitalize;
    }
.entry-footer
{
    border-top: 2px  solid black;
    background-color: #33383e;
    width: 100%;

    display: flex;
    flex-direction: row;
    justify-content: center;
    overflow: hidden;
    
}
    .entry-button-container
    {
        /* border: 2px solid red; */
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: row;
    }
        .entry-button-container > button 
        {
            background-color: whitesmoke;
            border: 2px solid black;
            width: 6.0em;
            height: 5.0em;
        }
            .entry-button-container > button:hover
            {
                background-color: gold;
            }
            
            .entry-button-left
            {
                margin-right: 6px;
            }
            .entry-button-container > button > *
            {
                font-size: 3.5em;
            }

    .reference-link-container
    {
        /* border: 2px solid red; */

        display: flex;
        justify-content: space-evenly;
        align-items: center;
        flex-direction: row;

        margin-left: 10px;
    }

/* phone medium */
@media only screen and (max-width: 425px) {
    .entry-footer
    {
        flex-direction: column;
        justify-content: center;
    }
    .reference-link-container
    {
        margin-left: 0px;
    }
} 
a{
    text-decoration: none;
}
.ref-container
{
    /* border: 2px solid red; */
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    flex-direction: column;

    width: 6em;
    height: 5em;

    overflow: hidden;
    margin-right: 5px; 
}
    .ref-container:hover
    {
        background-color: gold;
    }
    .ref-icon
    {
        margin-bottom: 0px;
        font-size: 2.2em;
        /* border: 2px solid red; */
    }
    .ref-name
    {
        margin-top: 0px;
        text-transform: capitalize;
        /* border: 2px solid blue;  */
        
    }
    .ref-container:hover
    {
        background-color: gold;
    }


@media only screen and (max-width: 425px) {
    .ref-container
    {
        border-radius: 6%;
        width: 4em;
        height: 4em;
    }   
    .ref-container > span
    {
        font-size: 1.8em;
    }
    .ref-container > p
    {
        font-size: 0.7em;
    }
}
